html {
  font-family: 'Open Sans', sans-serif;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

body {
  font-size: 1.6em; }

* {
  margin: 0;
  padding: 0;
  position: relative; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit; }

h1 {
  font-family: 'Roboto Slab', serif;
  font-weight: 700; }

p {
  font-weight: 300;
  line-height: 1.8em; }

.half {
  width: 50%; }
  @media (max-width: 767px) {
    .half {
      width: 100%; } }

.left {
  float: left; }

.right {
  float: right; }

@media (max-width: 767px) {
  .left,
  .right {
    float: none; } }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

.clearfix {
  display: inline-block; }

* html .clearfix {
  height: 1%; }

.clearfix {
  display: block; }

.wrap--outer {
  width: 1024px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .wrap--outer {
      width: 100%; } }

.wrap--inner {
  width: 90%;
  margin: 0 auto;
  padding: 0 .3em;
  border-left: 1px dashed #e2e2e2;
  border-right: 1px dashed #e2e2e2; }

#home {
  height: 80vh;
  color: #e2e2e2; }
  #home .wrap--outer {
    background-color: green;
    background-image: url("../images/hi_imGG_bg--green.jpg");
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    background-position: center center;
    height: 100%; }
  #home .wrap--inner {
    height: 100%; }
  #home .container {
    height: 200px;
    top: 0;
    bottom: 0;
    margin: auto;
    position: absolute; }
  #home h1 {
    font-size: 2em; }
